@font-face {
  font-family: Arial;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Arial;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

.page {
  --bg-color: #fff;
  --border-color: #e1e7ed;
  --box-shadow-active: #00000040;
  --txt-color: #3d4e61;
  flex-direction: column;
  row-gap: 20px;
  margin-top: 0;
  padding: 0;
  display: flex;
}

.form--big {
  margin-top: 20px;
}

.input {
  box-sizing: border-box;
  background: url("Search.ccd3c07b.svg") 0 0 / contain no-repeat;
  background-color: var(--bg-color);
  border: 1px solid var(--border-color);
  color: var(--txt-color);
  border-radius: 4px;
  width: 100%;
  font-family: Arial, sans-serif;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.input--big {
  background-position: 26px;
  background-size: 19px;
  min-height: 70px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.input--small {
  background-position: 13px;
  background-size: 11px;
  min-height: 42px;
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.input:hover {
  box-shadow: 0 4px 4px 0 var(--box-shadow-active);
}

.input:focus {
  background: linear-gradient(180deg, var(--bg-color) 0%, #f6f6f7 100%);
  filter: drop-shadow(0 4px 4px var(--box-shadow-active));
  outline: none;
  font-weight: 900;
}

.input::placeholder {
  color: var(--txt-color);
  font-family: Arial, sans-serif;
  font-weight: 300;
}
